@import '../index.scss';

/////////////////
// LOGO

.logo {
  height: 3.25rem;
  margin-left: 2rem;
}

/////////////////
// SEARCH

.search {
  flex: 0 0 40%;

  display: flex;
  justify-content: center;
  align-items: center;

  @media only screen and (max-width: 31.25em) {
    order: 1;
    flex: 0 0 100%;
    // background-color: var(--color-grey-light-2);
  }

  &__input {
    font-family: inherit;
    font-size: inherit;
    background-color: var(--color-grey-light-2);
    border: none;
    padding: 0.7rem 2rem;
    border-radius: 100px;
    width: 90%;
    transition: all 0.2s;
    margin-right: -3.25rem;

    &:focus {
      outline: none;
      width: 100%;
      background-color: var(--color-grey-light-3);
    }

    &::-webkit-input-placeholder {
      font-weight: 100;
      color: var(--color-grey-light-4);
    }
  }

  &__input:focus + &__button {
    background-color: var(--color-grey-light-3);
  }

  &__button {
    border: none;
    background-color: var(--color-grey-light-2);

    &:focus {
      outline: none;
    }
    &:active {
      transform: translateY(2px);
    }
  }

  &__icon {
    height: 2rem;
    width: 2rem;
    color: var(--color-grey-dark-3);
  }
}

.user-nav {
  align-self: stretch;
  display: flex;
  align-items: center;

  & > * {
    padding: 0 2rem;
    cursor: pointer;
    height: 100%;
    display: flex;
    align-items: center;
  }

  & > *:hover {
    background-color: var(--color-grey-light-2);
  }

  &__icon-box {
    position: relative;
  }
  &__icon {
    height: 2.5rem;
    width: 2.5rem;
    color: var(--color-grey-dark-2);
  }
  &__notification {
    font-size: 0.8rem;
    height: 1.75rem;
    width: 1.75rem;
    border-radius: 50%;
    background-color: var(--color-primary);
    color: #fff;
    position: absolute;
    top: 1.5rem;
    right: 1.1rem;
    display: flex;
    justify-content: center;
    align-items: center;
  }
  &__user {
  }
  &__user-photo {
    height: 3.75rem;
    border-radius: 50%;
    margin-right: 1rem;
  }
  &__user-name {
  }
}

//////////////
/// ICON

.side-nav {
  font-size: 1.4rem;
  list-style: none;
  margin-top: 3.5rem;

  @media only screen and (max-width: 56.25em) {
    display: flex;
    margin: 0;
  }

  &__item {
    position: relative;

    @media only screen and (max-width: 56.25em) {
      flex: 1;
    }

    &:not(:last-child) {
      margin-bottom: 0.5rem;

      @media only screen and (max-width: 56.25em) {
        margin-bottom: 0;
      }
    }
  }

  &__item ::before {
    content: '';
    position: absolute;
    top: 0;
    left: 0;
    height: 100%;
    width: 3px;
    background-color: var(--color-primary);

    transform: scaleY(0);
    transition: transform 0.2s, width cubic-bezier(1, 0, 0, 1) 0.4s 0.2s,
      background-color 0.1s;
  }

  &__item:hover ::before,
  &__item--active::before {
    transform: scaleY(1);
    width: 100%;
    z-index: -1;
  }

  &__item:active::before {
    background-color: var(--color-primary-light);
  }

  &__link span {
    @media only screen and (max-width: 25em) {
      display: none;
    }
  }

  &__link:link,
  &__link:visited {
    color: var(--color-grey-light-1);
    text-decoration: none;
    text-transform: uppercase;
    display: block;
    padding: 1.5rem 3rem;

    position: relative;
    z-index: 10;

    display: flex;
    align-items: center;

    @media only screen and (max-width: 56.25em) {
      justify-content: center;
      padding: 2rem;
    }

    @media only screen and (max-width: 37.5em) {
      flex-direction: column;
    }
  }

  &__icon {
    width: 2.5rem;
    height: 2.5rem;
    color: currentColor;
    margin-right: 2rem;
    @media only screen and (max-width: 37.5em) {
      margin-right: 0;
      margin-bottom: 1rem;
    }
  }
}

.legal {
  font-size: 1.2rem;
  color: var(--color-grey-light-4);
  text-align: center;
  padding: 2.5rem;

  @media only screen and (max-width: 56.25em) {
    display: none;
  }
}

.gallery {
  display: flex;

  &__photo {
    width: 100%;
    display: block;
  }
}

.overview {
  display: flex;
  align-items: center;
  border-bottom: 1px solid var(--color-grey-light-2);

  &__heading {
    font-size: 2.25rem;
    font-weight: 300;
    text-transform: uppercase;
    letter-spacing: 1px;
    padding: 1.5rem 3rem;

    @media only screen and (max-width: 37.5em) {
      font-size: 1.8rem;
      padding: 1.25rem 2rem;
    }
  }

  &__stars {
    display: flex;
    margin-right: auto;
  }

  &__icon {
    width: 2.5rem;
    height: 2.5rem;
    color: var(--color-primary);
  }

  &__location {
    font-size: 1.2rem;
    display: flex;
  }

  &__rating {
    align-self: stretch;

    text-align: center;
    background-color: var(--color-primary);
    color: #fff;
    padding: 1rem;

    @media only screen and (max-width: 37.5em) {
      padding: 0 1.5rem;
    }

    &-average {
      font-size: 2.25rem;
      font-weight: 300;

      @media only screen and (max-width: 37.5em) {
        font-size: 1.8rem;
      }
    }

    &-count {
      font-size: 0.8rem;
      text-transform: uppercase;
      @media only screen and (max-width: 37.5em) {
        font-size: 0.5rem;
      }
    }
  }
}

.btn-inline {
  border: none;
  color: var(--color-primary);
  font-size: inherit;
  border-bottom: 1px solid currentColor;
  padding: 2px;
  margin: 0 1rem;
  display: inline-block;
  background-color: transparent;
  cursor: pointer;
  transition: all 0.2s;
  &:hover {
    color: var(--color-grey-dark-1);
  }
  &:focus {
    outline: none;
    animation: pulsate 1s infinite;
  }
}

@keyframes pulsate {
  0% {
    transform: scale(1);
    box-shadow: none;
  }

  50% {
    transform: scale(1.05);
    box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.25);
  }

  100% {
    transform: scale(1);
    box-shadow: none;
  }
}

.paragraph:not(:last-of-type) {
  margin-bottom: 2rem;

  // border-bottom: 1px solid var(--color-grey-light-3);
  // padding-bottom: 3rem;
}

.list {
  margin: 3rem 0;
  padding: 3rem 0;

  border-bottom: 1px solid var(--color-grey-light-3);
  border-top: 1px solid var(--color-grey-light-3);
  display: flex;
  flex-wrap: wrap;

  &__item {
    flex: 0 0 50%;
    list-style: none;
    margin-bottom: 0.7rem;

    span {
      color: var(--color-primary);
      margin-right: 1rem;
    }
  }
}

.recommend {
  font-size: 1.3rem;

  color: var(--color-grey-dark-3);

  display: flex;

  justify-content: space-between;
  align-items: center;

  &__friends {
    display: flex;
  }

  &__photo {
    box-sizing: content-box; /// box sizing keep
    width: 4rem;
    height: 4rem;

    border-radius: 50%;
    border: 3px solid white;

    &:not(:last-child) {
      margin-right: -1rem;
    }
  }
}

.review {
  background-color: #fff;
  box-shadow: 0 1rem 2rem rgba(0, 0, 0, 0.1);
  border-bottom: 1px solid var(--color-grey-light-3);
  padding: 3rem;
  position: relative;
  overflow: hidden;

  margin-bottom: 5rem;

  @media only screen and (max-width: 56.25em) {
    padding: 2rem;
    margin-bottom: 3rem;
  }

  &__text {
    margin-bottom: 2rem;
    z-index: 10;
    position: relative;
  }

  &__user {
    display: flex;
    align-items: center;

    // margin: 1rem 3rem;
  }

  &__photo {
    width: 4rem;
    border-radius: 50%;
    margin-right: 1rem;
  }

  &__user-box {
    margin-right: auto;
  }

  &__user-name {
    font-size: 1.1rem;
    font-weight: 500;
  }

  &__user-date {
    font-size: 1rem;
    color: var(--color-grey-light-4);
  }

  &__rating {
    font-size: 1.6rem;
    color: var(--color-primary);
    font-weight: 500;
  }
  &::before {
    content: '\201C';
    position: absolute;
    top: -2.75rem;
    left: -1rem;
    line-height: 1;
    font-size: 20rem;
    font-family: sans-serif;
    color: var(--color-grey-light-2);
    z-index: 1;
  }
}

.cta {
  padding: 3.5rem 0;
  text-align: center;

  @media only screen and (max-width: 56.25em) {
    padding: 2.5rem 0;
  }

  &__book-now {
    text-transform: uppercase;
    font-size: 1.5rem;
    font-weight: 500;
    margin-bottom: 2rem;
  }
}

.btn {
  font-size: 1.5rem;
  font-weight: 300;
  text-transform: uppercase;
  padding: 1rem 1rem;
  overflow: hidden;
  cursor: pointer;

  color: #fff;

  border: none;
  border-radius: 100px;

  background-image: linear-gradient(
    to right,
    var(--color-primary-light),
    var(--color-primary-dark)
  );
  position: relative;

  & > * {
    display: inline-block;
    height: 100%;
    width: 100%;
    transition: all 0.2s;
  }

  &__visible {
    padding: 2rem 7.5rem;
  }

  &__invisible {
    position: absolute;
    padding: 3rem 3rem;
    left: 0;
    top: -100%;
  }

  &:hover {
    background-image: linear-gradient(
      to left,
      var(--color-primary-light),
      var(--color-primary-dark)
    );
  }

  &:focus {
    outline: none;
    animation: pulsate 1s infinite;
  }

  &:hover &__visible {
    transform: translateY(100%) !important;
  }
  &:hover &__invisible {
    top: 0;
  }
}
